<template>
  <div class="page">
    <el-breadcrumb separator="/" style="margin-bottom: 50px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/integral">商品列表</el-breadcrumb-item>
      <el-breadcrumb-item>创建</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form
      :inline="true"
      ref="form"
      @submit.native.prevent="onSubmit"
      :model="form"
      :rules="rules"
      class="form-search"
    >
      <el-row>
        <el-col>
          <el-form-item label="商品标题" prop="title">
            <el-input size="small" placeholder="请输入商品标题" v-model="form.title"></el-input>
            <el-input size="small" placeholder="请输入商品标题(英文)" v-model="form.title_en"></el-input>
            <el-input size="small" placeholder="请输入商品标题（越南）" v-model="form.title_yn"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="积分" prop="credit">
            <el-input size="small" placeholder="请输入积分" v-model.number="form.credit"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="排序" prop="sort">
            <el-input size="small" placeholder="请输入内容" v-model.number="form.sort"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="每日最大购买数量" prop="sort">
            <el-input size="small" placeholder="请输入内容" v-model="form.max_shop_num">
              <template slot="append">件,为0则无限制</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="是否上架" prop="status">
            <el-select size="small" v-model="form.status" clearable placeholder="请选择">
              <el-option v-for="(value, key) in config.status" :value="key" :label="value" :key="value" />
            </el-select>
          </el-form-item>
        </el-col>
        <el-form-item label="分类" prop="cat_id">
          <el-select size="small" v-model="form.cat_id" clearable placeholder="请选择">
            <el-option v-for="{ id, name } in config.cats" :value="id" :label="name" :key="id" />
          </el-select>
        </el-form-item>
        <el-col>
          <el-form-item label="标题图片" prop="thumbs">
            <el-upload
              :limit="5"
              :action="'/api/api/upload?ssid=' + ssid()"
              :on-success="fileSuccess"
              :file-list="file1"
              list-type="picture"
              :on-remove="onDelct1"
            >
              <el-button size="small" type="infor">点击上传</el-button>
            </el-upload>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="缩略图" prop="thumb">
            <el-upload
              :limit="1"
              :action="'/api/api/upload?ssid=' + ssid()"
              :on-success="fileSuccess1"
              :file-list="file"
              list-type="picture"
              :on-remove="onDelct1"
            >
              <el-button size="small" type="infor">点击上传</el-button>
            </el-upload>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="内容" style="display: flex;" class="content" prop="content">
            <vue-editor
              id="editor"
              useCustomImageHandler
              @imageAdded="handleImageAdded"
              v-model="form.content"
              style="flex: 1;"
            ></vue-editor>
          </el-form-item>
          <el-form-item label="内容（英文）" style="display: flex;" class="content" prop="content">
            <vue-editor
              id="editor"
              useCustomImageHandler
              @imageAdded="handleImageAdded"
              v-model="form.content_en"
              style="flex: 1;"
            ></vue-editor>
          </el-form-item>
          <el-form-item label="内容（越南）" style="display: flex;" class="content" prop="content">
            <vue-editor
              id="editor"
              useCustomImageHandler
              @imageAdded="handleImageAdded"
              v-model="form.content_yn"
              style="flex: 1;"
            ></vue-editor>
          </el-form-item>
        </el-col>

        <el-col>
          <el-button native-type="submit" type="primary">提交</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import { VueEditor } from 'vue2-editor'
export default {
  name: 'goodsCreate',
  components: {
    VueEditor,
  },
  data() {
    return {
      form: {
        credit: 0,
        title: '',
        title_en: '',
        title_yn: '',
        max_shop_num: 0,
        sort: 0,
        status: 'Y',
        thumb: '',
        thumbs: [],
        content: '',
        content_en: '',
        content_yn: '',
        cat_id: '',
      },
      rules: {
        title: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          {
            min: 3,
            max: 100,
            message: '长度在 3 到 100 个字符',
            trigger: 'blur',
          },
        ],
        credit: [
          { required: true, message: '积分不能为空' },
          { type: 'number', message: '积分为数字值' },
        ],
        sort: [
          { required: true, message: '积分不能为空' },
          { type: 'number', message: '排序为数字值' },
        ],
        max_shop_num: [
          { required: true, message: '限购数为空' },
          { type: 'number', message: '限购数为数字值' },
        ],
        status: [{ required: true }],
        cat_id: [{ required: true, message: '分类为空' }],
        content: [{ required: true, message: '商品内容为空' }],
        thumb: [{ required: true, message: '标题图片为空' }],
        thumbs: [{ type: 'array', required: true, message: '缩略图为空' }],
      },
      is_open_v: false,
      config: {
        status: {},
        type: {},
        is_low_risk: null,
        cats: null,
      },
      file: [],
      file1: [],
      date: new Date(),
      id: null,
    }
  },
  mounted() {
    this.id = this.$route.query.id
    var url = '/goods/goods/search'
    if (this.id) {
      url = '/goods/goods/view'
    }

    Fetch(url, { id: this.id }).then(r => {
      if (r.data.view) {
        if (r.data.view.if_open_vouchers == 'Y') {
          this.is_open_v = true
        } else {
          this.is_open_v = false
        }
        this.form = r.data.view
        this.config = r.data.config

        this.form.credit = Number(r.data.view.credit)
        this.form.max_shop_num = Number(r.data.view.max_shop_num)
        this.form.sort = Number(r.data.view.sort)

        this.form.thumb = r.data.view.thumb
        this.form.thumbs = r.data.view.thumbs
        if (r.data.view.thumb) {
          this.file = [
            {
              url: r.data.view.thumb,
              name: r.data.view.thumb,
            },
          ]
        }
        if (r.data.view.thumbs) {
          this.file1 = r.data.view.thumbs.map(item => {
            return {
              url: item,
              name: item,
            }
          })
        }

        if (r.data.view.begin_time > 0) {
          this.date = new Date(parseInt(r.data.view.begin_time) * 1000)
        }
      } else {
        this.config = r.data.config
      }
    })
  },
  methods: {
    ssid() {
      return localStorage.getItem('ssid')
    },
    vouchersHandle(res) {
      if (res) {
        this.form.if_open_vouchers = 'Y'
        this.is_open_v = true
        this.form.if_open_vouchers_name = '是'
      } else {
        this.form.if_open_vouchers = 'N'
        this.is_open_v = false
        this.form.if_open_vouchers_name = '否'
      }
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          var url = '/goods/goods/create'
          if (this.id) {
            url = '/goods/goods/update'
          }
          Fetch(url, {
            id: this.id,
            ...this.form,
          }).then(() => {
            Message.success('操作成功')
            this.$router.replace('/integral/goodslist')
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    fileSuccess(res, file) {
      this.file1.push(file)
      this.form.thumbs.push(res.data.file)
    },
    fileSuccess1(res, file) {
      console.log('thumb :>> ')
      this.file = [file]
      this.form.thumb = res.data.file
    },
    onDelct1(file, fileList) {
      this.file1 = fileList
      this.form.thumbs = fileList.map(item => item.name)
      console.log('object :>> ', file)
      console.log('object :>> ', fileList)
    },
    onDelct(file, fileList) {
      this.file = fileList
      this.form.thumb = fileList.map(item => item.name)
      console.log('object :>> ', file)
      console.log('object :>> ', fileList)
    },
  },
}
</script>
<style lang="less" scoped>
.page {
  padding: 0 0 40px;
}

.form-search {
  max-width: 1000px;

  /deep/ .el-input {
    width: 300px;
  }

  /deep/ .el-form-item__label {
    width: 140px;
    text-align: right;
  }
}
</style>
